@use '../components/functions';

*,*::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    width: 100%;
    background: url(../../images/Seamless-Wood-Texture-5.jpg);
    background-size: cover;
    overflow: hidden;
}

main {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: functions.toRem(10);
}